import React, { useState, useEffect } from "react";
import classNames from "classnames";
import { getDragon } from "../../api/app";
import DragonDesc from "../types";
import { Bars } from "react-loader-spinner";
import Carousel from "react-bootstrap/Carousel";
import "./Dragon.scss";

type Props = {
    id: string;
    isOpen: boolean;
    closePopup: Function;
};

export const Dragon: React.FC<Props> = ({ id, isOpen, closePopup }) => {
    const [dragon, setDragon] = useState<DragonDesc>();

    useEffect(() => {
        const dragonFormServer = async () => {
            const dragon = await getDragon(id);
            setDragon(dragon);
        };

        dragonFormServer();
    }, [id]);

    return (
        <>
            {!dragon ? (
                <div className="preloader">
                    <Bars
                        height="50"
                        width="50"
                        color="#4fa94d"
                        ariaLabel="bars-loading"
                        wrapperStyle={{}}
                        wrapperClass=""
                        visible={true}
                    />
                </div>
            ) : (
                <div
                    className={classNames("dragon", isOpen && "dragon_active")}
                >
                    <div className="dragon__block" onClick={() => closePopup()}>
                        <div className="dragon__close">Close</div>
                    </div>
                    <div className="dragon__container container">
                        <div className="dragon__pic">
                            <Carousel>
                                {dragon.flickr_images.map((img, ind) => (
                                    <Carousel.Item key={ind}>
                                        <img
                                            src={img}
                                            alt="img"
                                            className="d-block dragon__img"
                                        />
                                    </Carousel.Item>
                                ))}
                            </Carousel>
                        </div>

                        <div className="container__block">
                            <div className="dragon__title">{dragon.name}</div>
                            <div className="dragon__desc">
                                {dragon.description}
                            </div>
                            <a
                                href={dragon.wikipedia}
                                className="dragon__wiki"
                                target="_blank"
                            >
                                Find in Wikipedia
                            </a>
                            <ul className="dragon__params">
                                <li className="dragon__param">
                                    <div>Height:</div>
                                    <div>{dragon.height_w_trunk.meters}m.</div>
                                </li>
                                <li className="dragon__param">
                                    <div>Mass:</div>
                                    <div>{dragon.dry_mass_kg}kg.</div>
                                </li>
                                <li className="dragon__param">
                                    <div>Year:</div>
                                    <div>{dragon.first_flight}</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};
