import { useState, useEffect } from "react";
import { Dragon } from "./components/Dragon";
import { ListDragons } from "./components/ListDragons";
import PullToRefresh from "react-simple-pull-to-refresh";
import { Bars } from "react-loader-spinner";
import { getListDragons } from "./api/app";
import DragonDesc from "./components/types";
import "./App.scss";

function App() {
    const [popup, setPopup] = useState(false);
    const [dragonId, setDragonId] = useState("5e9d058759b1ff74a7ad5f8f");
    const [listDragons, setListDragons] = useState<DragonDesc[]>([]);

    const openPopup = (id: string) => {
        setDragonId(id);
        setPopup(true);
    };

    const closePopup = () => {
        setPopup(false);
    };

    const dragonsFormServer = async () => {
        const listDragons = await getListDragons();
        setListDragons((state) => [...state, ...listDragons]);
    };

    const handleRefresh = async () => {
        const listDragons = await getListDragons();
        setListDragons([...listDragons]);
    };

    useEffect(() => {
        dragonsFormServer();
    }, []);

    return (
        <div className="App">
            <PullToRefresh
                onRefresh={handleRefresh}
                refreshingContent={
                    <Bars
                        height="80"
                        width="80"
                        color="#4fa94d"
                        ariaLabel="bars-loading"
                        wrapperStyle={{ margin: "auto", display: "block" }}
                        wrapperClass=""
                        visible={true}
                    />
                }
            >
                <>
                    <h1 className="App__title">Spase-X</h1>
                    <ListDragons
                        open={openPopup}
                        popup={popup}
                        listDragons={listDragons}
                    />
                </>
            </PullToRefresh>

            <Dragon id={dragonId} isOpen={popup} closePopup={closePopup} />
        </div>
    );
}

export default App;
