import React, { useState, useEffect } from "react";
import DragonDesc from "../types";
import { Bars } from "react-loader-spinner";
import classNames from "classnames";
import "./ListDragons.scss";

type Props = {
    open: Function;
    popup: boolean;
    listDragons: DragonDesc[];
};

export const ListDragons: React.FC<Props> = ({ open, popup, listDragons }) => {
    return (
        <>
            {!listDragons ? (
                <div className="preloader">
                    <Bars
                        height="80"
                        width="80"
                        color="#4fa94d"
                        ariaLabel="bars-loading"
                        wrapperStyle={{}}
                        wrapperClass=""
                        visible={true}
                    />
                </div>
            ) : (
                <div
                    className={classNames(
                        "list-dragons",
                        popup && "list-dragons_passive"
                    )}
                >
                    <ul className="list-dragons__list">
                        {listDragons.map((dragon) => (
                            <li
                                key={dragon.id}
                                className="list-dragons__item item"
                                onClick={() => open(dragon.id)}
                            >
                                <img
                                    src={dragon.flickr_images[0]}
                                    alt="first photo"
                                    className="item__img"
                                />

                                <div className="item__title">{dragon.name}</div>
                            </li>
                        ))}
                    </ul>
                </div>
            )}
        </>
    );
};
